import React, { useState, useEffect } from 'react';


function Practice() {

  useEffect(() => {
    setInterval(()=> !document.hasFocus() ? document.location.reload(): '', 1000*60*15)
  })

  function random() {
    let li = [];

    for (let i = 0; i < 10; i++) {
      li.push(<li>Hello List {i} </li>)
    }
    return li;
  }
  const [count, setCount] = useState(random());


  const btnClick = () => {
    setCount(random())
  }

  return (

    <div>
      <h1>This is for pracitce reactjs</h1>
      <ul>
        {count}
      </ul>
      <button onClick={btnClick}> Click Me</button>

    </div>


  )
}

export default Practice;