import React, { useState, useEffect, useRef } from 'react';
import {
    makeStyles, Box, Container, Grid,
    ButtonBase, Paper, useMediaQuery
} from '@material-ui/core';

import InstagramIcon from '@material-ui/icons/Instagram';
import FacebookIcon from '@material-ui/icons/Facebook';
import TwitterIcon from '@material-ui/icons/Twitter';
// AOS SLIDER IMPORT 
import AOS from 'aos';
import './practice.css';


const useStyle = makeStyles((theme) => (
    {
        root: {
            boxSizing: 'border-box',
            fontFamily: 'sans-serif'
        },
        header: {
            textTransform: 'uppercase',
        },
        a_link: {
            display: 'inline-block',
            color: '#000',
            textDecoration: 'none'
        },
        gridPD: {
            padding: '8px'
        },
        btnClick: {
            width: '35px',
            height: '35px',
            background: '#B3B3B3'
        }
    }
))

export function PsdToReact(props) {
    const classes = useStyle();
    useEffect(() => {
        AOS.init();
    })
    return (
        <div className={classes.root} >
            <Header></Header>
            <Section1></Section1>
            <Section2></Section2>
            <Section3></Section3>
            <Section4></Section4>
            <Footer></Footer>

        </div>
    );
}
const Header = () => {

    const HeaderStyle = makeStyles(() => (
        {
            nikhilgrow: {
                flexGrow: 1
            },
            navSmMenu: {
                position: 'absolute',
                top: '100%',
                right: 0,
                background: '#F1F1F1',
                opacity: 1,
                pointerEvents: 'visible',

                '& a': {
                    display: 'block!important',
                    width: '100%',
                    '&:hover': {
                        background: '#B8B8B8'
                    }
                }
            },
            hide: {
                opacity: 0,
                pointerEvents: 'none',

            }
        }
    ))
    const classes = useStyle();
    const headerClasses = HeaderStyle()
    const navItem = [
        {
            id: 1,
            name: 'MINIMÃ˜',
            tag: 'div',
            padding: '.5rem 0rem',
            classes: headerClasses.nikhilgrow

        },
        {
            id: 2,
            name: "LIFESTYLE",
            padding: '.5rem 1rem',
            tag: 'a'
        },
        {
            id: 3,
            name: "PHOTODIARY",
            padding: '.5rem 1rem',
            tag: 'a'
        },
        {
            id: 4,
            name: "MUSIC",
            padding: '.5rem 1rem',
            tag: 'a'

        },
        {
            id: 5,
            name: "TRAVEL",
            padding: '.5rem 1rem',
            tag: 'a'

        }
    ]
    const sm = useMediaQuery('(max-width: 768px)')
    const navMenu =
        (sm ? navItem.slice(1) : navItem).map(v => {
            return (
                <Box className={`${classes.a_link} ${v.classes}`} component={v.tag} href="http://fb.me" key={v.id}
                px={1}
                    py={1}> <ButtonBase component="span" style={{ padding: v.padding }}> {v.name}</ButtonBase> </Box>
            )
        })
    const [togglenav, setTogglenav] = useState(false);
    const [btnRef, boxRef] = [useRef(), useRef()]

    document.onclick = (e) => {
        if (!btnRef.current?.contains(e.target) && !boxRef.current?.contains(e.target)) {
            setTogglenav(false)
        }
    }
    return (
        <Box data-aos="fadeUp" component="header" px={2} className={classes.header}>
            <Box component="nav" display="flex" maxWidth="md" >

                {
                    sm ?

                        <Box bgcolor="#F1F1F1" position="relative" className={`${classes.a_link} ${navItem[0].classes}`}
                            component={navItem[0].tag} px={2}
                            py={1} display="flex!important" justifyContent="space-between">

                            <ButtonBase component="span"
                                style={{ padding: navItem[0].padding }}> {navItem[0].name}

                            </ButtonBase>

                            <ButtonBase ref={btnRef} className={classes.btnClick} onClick={() =>
                                setTogglenav(!togglenav)} >= </ButtonBase>


                            <Box ref={boxRef} className={`${headerClasses.navSmMenu} ${togglenav ? '' : headerClasses.hide}`}
                                position="absolute"> {navMenu} </Box>
                        </Box>



                        :

                        navMenu





                }
            </Box>
            <div data-aos="fadeUp" className=" container-md header_body">
                <Box component="img" width={1} src={require('./img/minimo.jpg')}
                    alt="" />
            </div>
        </Box>
    )
}

