const Footer = () => {
    const FooterStyle = makeStyles(() => (
        {
            footer: {
                background: '#F0F0F0',
                '& .link': {
                    padding: '5px 8px'
                },
                '& .social_icon': {
                    verticalAlign: 'bottom'
                }

            }
        }
    ))
    const footerClass = FooterStyle();
    const footerText = [
        {
            id: 1,
            text: 'Terms and Conditions'
        },
        {
            id: 2,
            text: 'Privacy'
        },
        {
            id: 3,
            text: 'Follow',
            flexGrow: 1,
            textAlign: 'right',
            social_icon: <React.Fragment>
                <a className="social_icon" href="#"><FacebookIcon /></a>
                <a className="social_icon" href="#"> <TwitterIcon />
                </a><a className="social_icon" href="#"><InstagramIcon />
                </a></React.Fragment>
        }
    ]
    return (
        <Container data-aos="fade-up"
            data-aos-anchor-placement="center-bottom" component="footer" className={footerClass.footer}>
            <Box p={3} maxWidth="md">
                <Grid container>
                    {
                        footerText.map(v => {
                            const footerInner = makeStyles(() => (
                                {
                                    flex: {
                                        flexGrow: v.flexGrow,
                                        textAlign: v.textAlign
                                    }
                                }
                            ))
                            const inner = footerInner();
                            return (
                                <Grid key={v.id} item className={inner.flex} >
                                    <ButtonBase component="a" className="link" href="#"> {v.text}
                                    </ButtonBase>

                                    {v.social_icon}



                                </Grid>
                            )
                        })
                    }
                </Grid>
            </Box>
        </Container>
    )
}